import { Component, OnInit } from '@angular/core';
import CloudBingeJson from '../../assets/cloud-binge.json';

@Component({
  selector: 'app-video-search',
  templateUrl: './video-search.component.html',
  styleUrls: ['./video-search.component.sass']
})
export class VideoSearchComponent implements OnInit {

  searchResults = [];
  searchString = 'Prometheus';
  elasticIndex: any = null;

  constructor() { }

  ngOnInit(): void {
    this.elasticIndex = elasticlunr(function init() {
      this.addField('title');
      this.addField('description');
      this.setRef('videoUrl');
    });

    CloudBingeJson.forEach(video => {
      this.searchResults = this.elasticIndex.addDoc(video);
    });

    this.searchForTerm();

  }

  searchForTerm() {
    this.searchResults = this.elasticIndex.search(this.searchString).slice(0, 20);
  }

  addSearchTerm(newTerm: string) {
    this.searchString += (' ' + newTerm);
    this.searchForTerm();
  }


}
